import React,{useState}from 'react'
import {Link} from 'gatsby'
import styled from "styled-components";
import {Dropdown, Card, Accordion } from 'react-bootstrap'
import { Range } from 'rc-slider'
import 'rc-slider/assets/index.css'
import ReactSelect from '../ReactSelect/ReactSelect';
import {status,propertyType,bathrooms,bedrooms,garages,yearbuilt} from '../ReactSelect/SelectData'
	const DropdownIndicatorIcon=styled.span `
		margin-right:6px;
        &:after {
        display: inline-block;
        margin-left: .255em;
        vertical-align: .255em;
        content: "";
        border-top: .3em solid;
        border-right: .3em solid transparent;
        border-bottom: 0;
        border-left: .3em solid transparent;
    }
`
const AdvanceSearchWidgetSidebar=({style2,index})=>{
	const priceSlider = (value) => {
        document.getElementById("amount").value = "$" + value[0];
        document.getElementById("amount2").value = "$" + value[1];
	}

	
	const CheckBox=({id,label})=>{
	const [checked, setChecked] = useState(false);
	const ToggleAttribute =()=>{
	setChecked(!checked)
	}
		return(
		<div className="custom-control custom-checkbox">
			<input type="checkbox" className="custom-control-input" id={`${index}_${id}`} defaultChecked={checked}/>
			<label className="custom-control-label" htmlFor={`${index}_${id}`} onClick={ToggleAttribute}>{label}</label>
		</div>
		)
	}

    return (
			<ul className={!style2 ? "sasw_list mb0" : "sasw_list style2 mb0"}>
				<li className="search_area">
					<div className="form-group">
						<input type="text" className="form-control" id={`${index}_exampleInputNamsdsde1`} placeholder="keyword"/>
						<label htmlFor={`${index}_exampleInputNamsdsde1`}><span className="flaticon-magnifying-glass"></span></label>
					</div>
				</li>
				<li className="search_area">
					<div className="form-group">
						<input type="text" className="form-control" id={`${index}_exampleInputEmassil`} placeholder="Location"/>
						<label htmlFor={`${index}_exampleInputEmassil`}><span className="flaticon-maps-and-flags"></span></label>
					</div>
				</li>
				<li>
					<div className="search_option_two">
						<div className="candidate_revew_select">
							<ReactSelect data={status} style2={style2}/>
						</div>
					</div>
				</li>
				<li>
					<div className="search_option_two">
						<div className="candidate_revew_select">
							<ReactSelect data={propertyType} style2={style2}/>
						</div>
					</div>
				</li>
				<li>
					<div className="small_dropdown2">
						<Dropdown>
							<Dropdown.Toggle as="div" role="button" className='btn dd_btn' bsPrefix>
								<span>Price</span>
								<span className="float-right" style={style2 ? {color: "#484848"} : null}><DropdownIndicatorIcon/></span>
							</Dropdown.Toggle>

							<Dropdown.Menu className=" dd_content2">
								<div className="pricing_acontent">
									<input type="text" id={`${index}_amount`} className="amount" placeholder="$3000" /> 
									<input type="text" id={`${index}_amount2`} className="amount2" placeholder="$18000" />
									<Range min={2000} max={18990} defaultValue={[3000, 18000]} onChange={priceSlider} />
								</div>
							</Dropdown.Menu>
						</Dropdown>
					</div>
				</li>
				<li>
					<div className="search_option_two">
						<div className="candidate_revew_select">
							<ReactSelect data={bathrooms} style2={style2}/>
						</div>
					</div>
				</li>
				<li>
					<div className="search_option_two">
						<div className="candidate_revew_select">
							<ReactSelect data={bedrooms} style2={style2}/>
						</div>
					</div>
				</li>
				<li>
					<div className="search_option_two">
						<div className="candidate_revew_select">
							<ReactSelect data={garages} style2={style2}/>
						</div>
					</div>
				</li>
				<li>
					<div className="search_option_two">
						<div className="candidate_revew_select">
							<ReactSelect data={yearbuilt} style2={style2}/>
						</div>
					</div>
				</li>
				<li className="min_area style2 list-inline-item">
					<div className="form-group">
						<input type="text" className="form-control" id={`${index}_exampleInputName2fd`}placeholder="Min Area"/>
					</div>
				</li>
				<li className="max_area list-inline-item">
					<div className="form-group">
						<input type="text" className="form-control" id={`${index}_exampleInputNamefd3`}  placeholder="Max Area"/>
					</div>
				</li>
				<li>
				<Accordion className="panel-group" >
				<Card className="panel" bsPrefix>
					<Accordion.Toggle as='div' eventKey="0" className="panel-heading" >
						<h4 className="panel-title">
							<Link to="#panelBodyRating" className="accordion-toggle link" data-toggle="collapse" data-parent="#accordion"><i className="flaticon-more"></i> Advanced features</Link>
						</h4>
					</Accordion.Toggle>
					
					<Accordion.Collapse eventKey="0" className="panel-collapse collapse" >
					<Card.Body className="panel-body row" >
						<div className="col-lg-12">
							<ul className="ui_kit_checkbox selectable-list float-left fn-400">
								<li>
									<CheckBox id="customCheck1" label="Air Conditioning"/>
								</li>
								<li>
									<CheckBox id="customCheck2" label="Barbeque"/>
								</li>
								<li>
									<CheckBox id="customCheck10" label="Gym"/>
								</li>
								<li>
									<CheckBox id="customCheck5" label="Microwave"/>
								</li>
								<li>
									<CheckBox id="customCheck6" label="TV Cable"/>
								</li>
								<li>
									<CheckBox id="customCheck276" label="Lawn"/>
								</li>
								<li>
									<CheckBox id="customCheck11" label="Refrigerator"/>
								</li>
								<li>
									<CheckBox id="customCheck3" label="Swimming Pool"/>
								</li>
							</ul>
							<ul className="ui_kit_checkbox selectable-list float-right fn-400">
								<li>
									<CheckBox id="customCheck12" label="WiFi"/>
								</li>
								<li>
									<CheckBox id="customCheck14" label="Sauna"/>
								</li>
								<li>
									<CheckBox id="customCheck7" label="Dryer"/>
								</li>
								<li>
									<CheckBox id="customCheck9" label="Washer"/>
								</li>
								<li>
									<CheckBox id="customCheck13" label="Laundry"/>
								</li>
								<li>
									<CheckBox id="customCheck8" label="Outdoor Shower"/>
								</li>
								<li>
									<CheckBox id="customCheck15" label="Window Coverings"/>
								</li>
							</ul>
						</div>
					</Card.Body>
					</Accordion.Collapse>
				</Card>
				</Accordion>
				</li>
				<li>
					<div className="search_option_button">
						<button type="submit" className="btn btn-block btn-thm">Search</button>
					</div>
				</li>
			</ul>
    )
}

export default AdvanceSearchWidgetSidebar
